.main {
  padding: 455px 0 300px 0;
  background-color: #060606;
  background-image: url("../img/main_bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  &__bg2 {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 533px;
    pointer-events: none;
  }
  &__img {
    position: absolute;
    z-index: 0;
    top: 175px;
    right: 40px;
    width: 981px;
  }
  &__img2 {
    position: absolute;
    z-index: 0;
    top: 30px;
    right: calc(100% - 55px);
  }
  &__inner {
    position: relative;
  }
  &__wrapp {
    position: relative;
    z-index: 1;
  }
  &__subtitle {
    font-size: 36px;
    line-height: 120%;
    font-weight: 100;
    color: #fdfdfd;
  }
  &__title {
    font-size: 48px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #fdfdfd;
    margin-top: 20px;
    span {
      color: #fec123;
    }
  }
  &__info {
    font-size: 36px;
    line-height: 120%;
    letter-spacing: 0.2em;
    color: #fdfdfd;
    margin-top: 30px;
  }
  &__btn {
    width: 428px;
    height: 105px;
    background-color: #0b7578;
    background-image: linear-gradient(0.0deg, #d18707 0%, #fec123 100%);
    border-radius: 28.5px;
    border-style: solid;
    border-width: 2px;
    border-image-source: linear-gradient(0.0deg, #ebcb72 0%, #cea33c 100%);
    background-origin: border-box;
    font-size: 36px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
    margin-top: 140px;
    &:hover {
      background-image: linear-gradient(0.0deg, #9a6403 0%, #d49707 100%);
    }
  }
}
.services {
  padding: 0 0 225px;
  background-color: #060606;
  overflow: hidden;
  margin-top: -200px;
  &__inner {
    max-width: 1115px;
    margin: 0 auto;
  }
  &__head {
    display: flex;
    align-items: center;
  }
  &__body {
    margin-top: 50px;
  }
  &__tab {
    font-family: 'MyriadPro';
    height: 55px;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: 0.1em;
    color: #fdfdfd;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding: 0 30px;
    position: relative;
    &:not(:first-child) {
      margin-left: 55px;
    }
    &--active {
      border: 4.5px solid #49841a;
      border-radius: 10px;
    }
  }
  &__content {
    position: relative;
    display: none;
    &--active {
      display: block;
    }
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    opacity: 0;
    transition: all .3s;
    pointer-events: none;
    height: auto;
    min-height: auto;
    &-next,
    &-active {
      opacity: 1;
      pointer-events: unset;
    }
  }
  .swiper-button-next {
    right: -70px;
    z-index: 1;
    &:after {
      color: #ffffff;
    }
  }
  .swiper-button-prev {
    left: -200px;
    z-index: 1;
    &:after {
      color: #ffffff;
    }
  }
  .swiper-button-disabled {
    opacity: 0;
  }
  &__content {
    position: relative;
  }
  &__card {
    height: 100%;
    background: rgba(47, 46, 45, 0.7);
    border-radius: 20px;
    position: relative;
    padding: 26px 43px 56px 130px;
    text-align: center;
    &-img {
      position: absolute;
      top: 0;
      left: -140px;
      width: 231px;
      img {
        width: 100%;
      }
    }
    &-title {
      font-family: 'Rhythm-and-Jazz-Decor';
      font-size: 36px;
      line-height: 120%;
      letter-spacing: 0.1em;
      color: #fdfdfd;
      padding-bottom: 13px;
      border-bottom: 3px solid #d1ab44;
    }
    &-text {
      font-family: 'MyriadPro';
      font-size: 14px;
      line-height: 120%;
      letter-spacing: 0.1em;
      color: #fdfdfd;
      padding-top: 13px;
    }
    &-price {
      font-size: 30px;
      line-height: 120%;
      letter-spacing: 0.1em;
      color: #fdfdfd;
      margin-top: 20px;
    }
    &-btn {
      position: absolute;
      top: calc(100% + 4px);
      right: 40px;
      width: 245px;
      height: 61px;
      background-color: #0b7578;
      background-image: linear-gradient(0.0deg, #d18707 0%, #fec123 100%);
      border-radius: 28.5px;
      border-style: solid;
      border-width: 2px;
      border-image-source: linear-gradient(0.0deg, #ebcb72 0%, #cea33c 100%);
      background-origin: border-box;
      font-size: 20.3px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffffff;
      &:hover {
        background-image: linear-gradient(0.0deg, #9a6403 0%, #d49707 100%);
      }
    }
  }
}

@media screen and (max-width: 1800px) {
  .main {
    &__img {
      top: 50%;
      transform: translate(0, -50%);
      width: 700px;
    }
    &__img2 {
      right: calc(100% - 150px);
    }
  }
}

@media screen and (max-width: 1550px) {
  .services {
    .swiper-buttons {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-top: 120px;
    }
    .swiper-button-prev {
      position: static;
      order: -1;
    }
    .swiper-button-next {
      position: static;
      margin-left: 25px;
    }
    .swiper-button-disabled {
      opacity: 0.3;
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    &__img {
      top: unset;
      bottom: 300px;
      transform: unset;
      width: 600px;
    }
  }
  .services {
    &__card {
      &-img {
        width: 150px;
        top: 50%;
        left: -75px;
        transform: translate(0, -50%);
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .services {
    &__card {
      &-img {
        top: 50%;
        left: -30px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    padding: 400px 0 300px;
    &__img {
      width: 450px;
    }
  }
  .services {
    &__card {
      padding: 26px 26px 56px 110px;
      &-img {
        width: 100px;
        left: 0;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 350px 0 300px;
    &__img {
      width: 300px;
    }
    &__subtitle {
      font-size: 28px;
    }
    &__title {
      font-size: 42px;
    }
    &__info {
      font-size: 28px;
    }
    &__btn {
      width: 300px;
      height: 60px;
      font-size: 28px;
      margin-top: 60px;
    }
  }
  .services {
    padding: 0 0 100px 0;
    &__tab {
      font-size: 20px;
      padding: 0 20px;
      &:not(:first-child) {
        margin-left: 30px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    padding: 230px 0 300px;
    &__img,
    &__img2 {
      display: none;
    }
    &__subtitle {
      font-size: 24px;
    }
    &__title {
      font-size: 38px;
      margin-top: 10px;
    }
    &__info {
      font-size: 24px;
      margin-top: 10px;
    }
    &__btn {
      width: 250px;
      font-size: 24px;
      margin-top: 30px;
    }
  }
  .services {
    &__card {
      &-title {
        font-size: 28px;
      }
      &-price {
        font-size: 24px;
      }
      &-btn {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .services {
    &__head {
      flex-direction: column;
      align-items: start;
    }
    &__tab {
      &:not(:first-child) {
        margin: 30px 0 0 0;
      }
    }
  }
}