.header {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  .container {
    max-width: 1645px;
  }
  &-top {
    position: relative;
    padding-top: 65px;
    padding-right: 95px;
    &__img {
      position: absolute;
      top: 0;
      right: 0;
    }
    &__inner {
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
    &__location {
      display: flex;
      align-items: center;
      &-img {
        width: 24px;
        img {
          width: 100%;
        }
      }
      &-desc {
        margin-left: 20px;
        span:nth-child(1) {
          display: block;
          font-size: 20.9px;
          line-height: 120%;
          font-weight: 400;
          text-transform: uppercase;
          color: #fdfdfd
        }
        span:nth-child(2) {
          display: block;
          font-size: 14.9px;
          line-height: 120%;
          color: #4a851b;
          margin-top: 5px;
        }
      }
    }
    &__tel {
      display: flex;
      align-items: center;
      margin-left: 60px;
      &-img {
        width: 24px;
        img {
          width: 100%;
        }
      }
      &-desc {
        margin-left: 20px;
        span:nth-child(1) {
          display: block;
          font-size: 20.9px;
          line-height: 120%;
          font-weight: 400;
          text-transform: uppercase;
          color: #fdfdfd
        }
        span:nth-child(2) {
          display: block;
          font-size: 14.9px;
          line-height: 120%;
          color: #4a851b;
          margin-top: 5px;
        }
      }
    }
    &__burger {
      width: 32px;
      height: 24px;
      position: relative;
      z-index: 6;
      cursor: pointer;
      margin-left: 15px;
      display: none;
      span {
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background: #4a851b;
        transition: all 0.5s;
      }
      span:nth-child(1) {
        top: 0;
      }
      span:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      span:nth-child(3) {
        bottom: 0;
      }
      &--active {
        span:nth-child(1) {
          transform: rotate(-45deg);
          top: 11px;
        }
        span:nth-child(2) {
          display: none;
        }
        span:nth-child(3) {
          transform: rotate(45deg);
          bottom: 11px;
        }
      }
    }
  }
  &-bottom {
    margin-top: -10px;
    &__inner {
      display: flex;
      align-items: center;
    }
    &__logo {
      width: 354px;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      img {
        width: 100%;
      }

    }
    &__list {
      display: flex;
      align-items: center;
      margin-left: 150px;
      li {
        font-size: 20.9px;
        line-height: 120%;
        text-transform: uppercase;
        color: #fdfdfd;
        &:hover {
          color: #4d891e;
        }
        &:not(:first-child) {
          margin-left: 60px;
        }
      }
      .active-link {
        color: #4d891e;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &-bottom {
      &__logo {
        width: 300px;
      }
      &__list {
        margin-left: 80px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &-top {
      padding-right: 0;
      &__img {
        display: none;
      }
      &__location {
        &-img {
          width: 18px;
        }
        &-desc {
          margin-left: 12px;
          span:nth-child(1) {
            font-size: 18px;
          }
        }
      }
      &__tel {
       margin-left: 40px;
        &-desc {
          margin-left: 12px;
          span:nth-child(1) {
            font-size: 18px;
          }
        }
      }
   }
    &-bottom {
      &__logo {
        width: 250px;
      }
      &__list {
        li {
          font-size: 18px;
          &:not(:first-child) {
            margin-left: 40px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &-top {
      &__location {
        &-desc {
          display: none;
        }
      }
      &__tel {
        margin-left: 20px;
        &-desc {
          display: none;
        }
      }
      &__burger {
        display: block;
      }
    }
    &-bottom {
      margin-top: 0;
      &__logo {
        width: 150px;
        margin-top: -65px;
      }
      &__list {
        position: fixed;
        z-index: 5;
        top: 0;
        left: -105%;
        width: 100%;
        height: 100vh;
        background: #000000;
        margin-left: 0;
        flex-direction: column;
        padding: 150px 30px 30px;
        transition: all 0.5s;
        &--active {
          left: 0;
        }
        li {
          &:not(:first-child) {
            margin: 15px 0 0 0;
          }
        }
      }
    }
  }
}